import { useEffect, useState } from 'react';
import './App.css';
import Header from './components/Header';
import Footer from './components/Footer';
import { Routes, Route, useLocation } from 'react-router-dom';

import HomePage from "./pages/home/page.jsx"
import AboutPage from "./pages/sobre/page.jsx"
import ContactPage from './pages/contato/page.jsx';
import ServicesPage from './pages/servicos/page.jsx';
import Partners from './pages/parceiros/page.jsx';
import Products from './pages/produtos/page.jsx';

function App() {
  const { pathname } = useLocation()

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [pathname])

  const [language, setLanguage] = useState("en")

  return (
    <div className="App">
      <Header language={language} setLanguage={setLanguage}/>
      <Routes>
        <Route element={<HomePage language={language} />} path='/' />
        <Route element={<AboutPage language={language}/>} path='/about'/>
        <Route element={<ContactPage language={language}/>} path='/contact'/>
        <Route element={<ServicesPage language={language}/>} path='/commodities'/>
        <Route element={<Partners language={language}/>} path='/partners'/>
        <Route element={<Products language={language}/>} path='/finance'/>
      </Routes>
      <Footer language={language} />
    </div>
  );
}

export default App;
